@use "variables" as v;

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-family: "Josefin Sans", sans-serif;
  font-size: 62.5%;
}

body {
  background-image: url(../images/bg-pattern-desktop.svg);
  // background-repeat: no-repeat;
  background-size: contain;
  background-color: linear-gradient(45deg, hsl(0, 0%, 100%), hsl(0, 100%, 98%));
}

a:link,
a:visited {
  color: v.$s-red;
}

//! Removing all button styling:

button,
input[type="submit"],
input[type="reset"] {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}
