@use "./variables" as v;

input:focus {
  outline: none !important;
  border: 1px solid v.$dg-red;
}

.landing {
  display: flex;
  justify-content: center;
  &_article {
    display: flex;
    flex-direction: row;
    overflow: hidden;
    &-left {
      padding: 11rem 19rem 11rem 0;
    }

    &-logo {
      width: 25rem;
      margin-bottom: 17rem;
    }

    &-photo {
      display: none;
    }

    &-content {
      width: 44rem;
    }

    &-h1 {
      font-weight: 300;
      font-size: 6.25rem;
      line-height: 7rem;
      text-transform: uppercase;
      letter-spacing: 2rem;
      color: v.$d-red;
      margin-bottom: 2.5rem;

      &_bold {
        font-weight: 600;
        color: v.$dg-red;
      }
    }

    &-p {
      font-size: 1.6rem;
      line-height: 3rem;
      color: v.$d-red;
      margin-bottom: 5rem;
    }
  }

  &_form {
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 45rem;
    align-items: center;
  }

  &_newsletter {
    margin-bottom: 10rem;
    &-input {
      position: relative;
      width: 40rem;
      padding: 2rem;
      padding-left: 4rem;
      font-family: "Josefin Sans", sans-serif;
      font-size: 1.6rem;
      color: v.$d-red;
      border: 1px solid v.$d-red;
      border-radius: 5rem;
    }

    &-input_error {
      outline: 2px solid v.$s-red;
      outline-offset: -2px;
    }
    &-input_success {
      outline: 2px solid v.$s-green;
      outline-offset: -2px;
    }

    &-submit {
      position: absolute;
      right: 0;
      width: 12rem;
      padding: 3.1rem;
      background: url(../images/icon-arrow.svg) center no-repeat,
        linear-gradient(to right, hsl(0, 80%, 86%), hsl(0, 74%, 74%));
      border-radius: 5rem;

      &:hover {
        background: url(../images/icon-arrow.svg) center no-repeat,
          hsl(0, 80%, 86%);
        box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
      }
    }

    &-submit-arrow {
      // padding: 2rem;
      border: 1px solid black;
    }
  }

  &_photo {
    object-fit: cover;
  }
}

//* Errors

.message_container {
  position: relative;
}

//? Icon

.error_icon {
  position: absolute;
  right: 13rem;
  width: 3rem;
  height: 3rem;
  background: url(../images/icon-error.svg) center no-repeat;
  display: none;
}

//? Text

.error_text {
  position: absolute;
  margin: 2rem 0 0 4rem;
  font-size: 1.4rem;
  color: v.$d-red;
  display: none;
}

//? Sucess Text

.success_text {
  position: absolute;
  margin: 2rem 0 0 4rem;
  font-size: 1.4rem;
  color: v.$s-green;
  display: none;
}

//? Show errors:

.show_error {
  display: block;
}

//* Media queries

@media only screen and (max-width: 75rem) {
  .landing {
    &_article {
      &-left {
        padding: 0;
        align-self: center;
        width: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
      }

      &-logo {
        width: 13rem;
        margin: 3rem 0 3rem 3rem;
        align-self: flex-start;
      }

      &-photo {
        width: 36rem;
        height: 25rem;
        display: block;
        object-fit: cover;
      }
      &-content {
        width: 35rem;
        margin-top: 6.4rem;
      }
      &-h1 {
        font-size: 4rem;
        line-height: 4rem;
        letter-spacing: 1.4rem;
      }

      &-p {
        line-height: 2.7rem;
      }
      & h1,
      p {
        text-align: center;
      }
    }
    &_photo {
      display: none;
    }
  }
  .landing_form {
    display: flex;
    justify-content: space-between;
  }
  .landing_newsletter-input {
    width: 32rem;
    padding: 1.5rem;
  }
  .landing_newsletter-submit {
    // position: static;
    right: 10rem;
    margin-right: auto;
    width: 8rem;
    padding: 2.55rem;
  }
  .error_icon {
    right: 19rem;
  }
}
